@import url(https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;1,300;1,400&display=swap);
:root{--primary-font: 'Titillium Web', sans-serif;--brand-color: #1b7cf8;--bg-color: #eeeeee;--text-black: #211f1f;--white: #ffffff;--text-gray: #9f9f9f;--black-1: #1e2022;--red: #de4437}*{font-family:'Titillium Web', sans-serif;font-family:var(--primary-font)}html{color:#211f1f;color:var(--text-black)}

.Header{grid-column:1/-1;display:flex;justify-content:space-between;align-items:center;padding:0 16px}.Header__headline-wrap{font-size:24pt;font-weight:600;color:#ffffff;display:flex;margin:0}.Header__back-btn,.Header__search-btn{background:transparent;outline:none;box-shadow:none;border:none}.Header__back-btn{margin-right:12px}.Header__back-btn-icon{height:24px}.Header__search-btn-icon{height:24px}

.Card__poster{width:100%;max-width:100%}.Card__headline{font-size:16pt;margin:16px 0 0;color:#ffffff;font-weight:normal}

.ScrollableCards{grid-column:1/-1;overflow:auto;padding:0 16px 16px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-gap:90px 30px}

.App{background-color:#171717;height:calc(100vh - 30px);padding:30px 0 0;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto 1fr;grid-gap:36px 30px}

